<template>
	<div>
		<div id="viewer"></div>
		<el-button type="primary" @click="addMark">新增一个标记</el-button>
		<el-select v-model="value" placeholder="请选择新增标记类型">
		    <el-option
		      v-for="item in options"
		      :key="item.value"
		      :label="item.label"
		      :value="item.value">
		    </el-option>
		</el-select>
		<el-button type="success" @click="stopPoint">完成</el-button>
		<el-button type="danger" @click="backPoint">撤销</el-button>
	</div>
</template>

<script>
	let numberObj = {
		polygonRad:1,
		polylineRad:2,
		image:1
	}
import * as PhotoSphereViewer from 'photo-sphere-viewer';
import MarkersPlugin from 'photo-sphere-viewer/dist/plugins/markers'
export default {
	data() {
		return {
			options: [{
			          value: 'polygonRad',
			          label: '多边形'
			        }, {
			          value: 'polylineRad',
			          label: '线段'
			        },{
						value:'image',
						label:'图片'
					}],
			value: 'polygonRad',
			longitude:'',
			latitude:'',
			isImg:require('../assets/logo.png')
		};
	},
  mounted() {
	  this.isAdd = false;
	  this.nowMarkers = '';
	  this.polygonRad = [];
  	this.viewer = new PhotoSphereViewer.Viewer({
  	  container: document.querySelector('#viewer'),
  	  panorama:require('../assets/2294472375_24a3b8ef46_o.jpg'),
	  moveInertia:false,
	  plugins: [
	      [MarkersPlugin, {
	        markers: [ 
	          {
	            id: 'circle',
	            tooltip:'A circle of radius 30',
	            circle:30,
	            svgStyle:{
	            fill:'rgba(255,255,0,0.3)',
	            stroke:'yellow',
	            strokeWidth:'2px',
	            },
	            longitude:-0.5,
	            latitude:-0.28,
	            anchor:'center right'
	          },{
				id:'rect',
				tooltip:'<div>宽度<input v-model="width" @input="isInput" placeholder="宽度" /></div>',
				rect: [100, 50],
				svgStyle:{
				fill:'url(#points)', //'rgba(255,0,0,0.3)',
				stroke:'rgba(255, 0, 50, 0.8)',
				strokeWidth:'2px',
				// fill:'rgba(255,255,0,0.3)',
				// stroke:'yellow',
				// strokeWidth:'2px',
				},
				longitude: '-100deg',
				latitude: '45deg',
				scale: [0.5, 1],
				anchor:'center bottom',
			  },
			  {
			      id: 'img1',
			      image: require('../assets/logo.png'),
				  width:100,
				  height:100,
				  longitude:0,
				  latitude:0
			    },
				{
					id: 'polyline',
					polylineRad: [5.924, 0.064, 5.859, -0.061, 5.710, -0.132, 5.410, -0.287, 4.329, -0.490, 3.838, -0.370, 3.725, -0.241],
					svgStyle:{
					  stroke: 'rgba(80, 100, 50, 0.8)',
					  strokeLinecap :'round',
					  strokeLinejoin:'round',
					  strokeWidth:'10px',
					},
					tooltip:'This is a track'
				}
				
	        ],
	      }], 
	    ],
  	});
	console.log(this.viewer)
	this.viewer.on('click', (e, data) => {
	  console.log(`${data.rightclick ? 'right clicked' : 'clicked'} at longitude: ${data.longitude} latitude: ${data.latitude}`);
	  if(this.isAdd){
		  if(this.value == 'polygonRad' || this.value == 'image'){
			  this.polygonRad.push([data.longitude,data.latitude])
		  }else if(this.value == 'polylineRad'){
			  this.polygonRad.push(data.longitude);
			  this.polygonRad.push(data.latitude);
		  }
		  this.changeMark();
	  }
	});
	this.markersPlugin = this.viewer.getPlugin(MarkersPlugin);
	this.markersPlugin.on('select-marker', (e, marker,data) => {
	  console.log(e)
	  console.log(marker)
	  if (data.dblclick) {
	  	console.log('dblClick')
	    this.markersPlugin.removeMarker(marker);
	  }
	  // this.nowMarkers = marker.config.id;
	});
  },
  methods: {
	  backPoint(){
		  this.polygonRad.splice(-1,numberObj[this.value]);
		  this.changeMark();
	  },
	  stopPoint(){
		this.isAdd = false;  
	  },
	  addMark(){
		  this.polygonRad = [];
		  this.nowMarkers = `markers${Math.random(9999)}`;
		  this.isAdd = true;
	  },
	  changeMark(){
		  let tempList = JSON.parse(JSON.stringify(this.polygonRad))
		  if(tempList.length == 0){
				this.markersPlugin.removeMarker(this.nowMarkers);
		  }else if(tempList.length == numberObj[this.value] ){
			  let svgPolygonRad = 
			  {
				polygonRad:{
				  id:this.nowMarkers,
				  tooltip:'<div style="display: flex;align-items: center;justify-content: center;flex-direction: column;height:12.5rem;"><img style="width: 5rem;height: 5rem;" src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3184273983,1374445179&fm=26&gp=0.jpg" /><div>介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍</div></div>',
				  svgStyle:{
				  fill:'rgba(255,255,0,0.3)',
				  stroke:'yellow',
				  strokeWidth:'2px',
				  }
				},
				polylineRad:{
					id:this.nowMarkers,
					tooltip:'<div style="display: flex;align-items: center;justify-content: center;flex-direction: column;height:12.5rem;"><img style="width: 5rem;height: 5rem;" src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3184273983,1374445179&fm=26&gp=0.jpg" /><div>介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍</div></div>',
					svgStyle:{
					  stroke: 'rgba(80, 100, 50, 0.8)',
					  strokeLinecap :'round',
					  strokeLinejoin:'round',
					  strokeWidth:'10px',
					}
				},
				image:{
					id:this.nowMarkers,
					image:this.isImg,
					width:100,
					height:100,
					longitude:tempList[0][0],
					latitude:tempList[0][1]
				}
			  }
			  let temp = svgPolygonRad[this.value];
			  if(this.value != 'image') temp[this.value] = [[99,99],[99,88]];
			  console.log(temp)
			  this.markersPlugin.addMarker(temp);
		  }else{
				if(this.value != 'image'){
				  let updateTemp = {
					id:this.nowMarkers
				  }
				  updateTemp[this.value] = tempList;
				  this.markersPlugin.updateMarker(updateTemp);
				}
		  }
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.editBox{
		display: flex;
	}
#viewer {
    width: 100vw;
    height: 90vh;
  }
</style>
